<template>
  <section class="card">
    <div class="card-header">
      <div class="utils__title">
        <strong>Create an issue</strong>
      </div>
    </div>
    <div class="card-body">
      <div :class="$style.addPost">
        <a-form class="mt-3" :form="form" @submit="handleSubmit">
          <div class="form-group">
            <a-form-item label="Subject">
              <a-input v-decorator="['subject',{ rules: [{ required: true, message: 'Please give a subject' }]}]" placeholder="Subject"/>
            </a-form-item>
            <a-form-item label="Related Issues">
              <a-select initialValue="shipment" v-decorator="['issue_type',{ rules: []}]" style="width: 100%">
                <a-select-option value="reports">Reports</a-select-option>
                <a-select-option value="shipment">Shipment</a-select-option>
                <a-select-option value="expenditure">Expenditure</a-select-option>
                <a-select-option value="income">Income</a-select-option>
              </a-select>
            </a-form-item>
            <a-form-item label="Priority">
              <a-select initialValue="high" v-decorator="['priority',{ rules: [{ required: true, message: 'Please set a priority level' }]}]" style="width: 100%">
                <a-select-option value="3">Critical</a-select-option>
                <a-select-option value="2">Medium</a-select-option>
                <a-select-option value="1">Low</a-select-option>
              </a-select>
            </a-form-item>
          </div>
          <div class="form-group">
            <div class="form-group">
              <a-form-item label="Message">
                <a-textarea v-decorator="['message',{ rules: [{ required: true, message: 'Please give the details of your issue' }]}]" placeholder="Issue Description" />
              </a-form-item>
            </div>
            <a-form-item>
              <div :class="$style.submit">
                <span class="mr-3">
                  <a-button type="primary" htmlType="submit">Submit Issue</a-button>
                </span>
                <a-button @click="handleReset" type="danger">Discard</a-button>
              </div>
            </a-form-item>
          </div>
        </a-form>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'index',
  data() {
    return {
      form: this.$form.createForm(this),
    }
  },
  methods: {
    handleReset() {
      this.form.resetFields()
    },
    handleSubmit(e) {
      e.preventDefault()
      this.form.validateFields((err, values) => {
        if (!err) {
          this.$nprogress.start()
          const requestPayload = {
            priority_level: values.priority,
            subject: values.subject,
            details: values.message,
            issue_type: values.issue_type,
          }

          this.axios.post('issues/store', requestPayload)
            .then(response => {
              this.$nprogress.done()
              // console.log('stats', response.status)
              if (response.status === 200) {
                this.form.resetFields()
                this.$notification['success']({
                  message: 'Issues Creation',
                  description: 'You have successfully Created an issue',
                })
              }
            })
            .catch(error => {
              // console.log('error', error.response.data.data)
              if (error.response.status === 401) {
                this.$notification['warning']({
                  message: 'Issues Creation',
                  description: 'Sorry you have a validation error ',
                })
              } else {
                this.$notification['warning']({
                  message: 'Issues Creation',
                  description: 'Sorry an error occurred.',
                })
              }
            })
        }
      })
    },
    handleChange(info) {
      const status = info.file.status
      if (status !== 'uploading') {
        console.log(info.file, info.fileList)
      }
      if (status === 'done') {
        this.$message.success(`${info.file.name} file uploaded successfully.`)
      } else if (status === 'error') {
        this.$message.error(`${info.file.name} file upload failed.`)
      }
    },
  },
}
</script>

<style lang="scss" module>
  @import "./style.module.scss";
</style>
